import React, { useState, useEffect } from "react";
import { ReactWidget } from "@jupyterlab/apputils";
import Container from "react-bootstrap/Container";
import { requestAPI } from "./handler";
import CardGrid from "./components/kscardgrid";
import { SuccessAlertBox } from "./components/alerts";
import { KsForm } from "./components/ksform";
import "bootstrap/dist/css/bootstrap.min.css";
/**
 * React component for listing the possible
 * ipykernel options.
 *
 * @returns The React component.
 */
const KernelManagerComponent = () => {
    const [data, setData] = useState({});
    const [showForm, setShowForm] = useState(false);
    const [kernelFormData, setKernelFormData] = useState({});
    const [selectedKernelName, setSelectedKernelName] = useState('');
    const [cardData, setCardData] = useState([]);
    const [schema, setSchema] = useState({});
    const [alertBox, setAlertBox] = useState(false);
    /**
     * Handles the Kernel Selection
     * at the select screen.
     */
    const handleSelectKernelspec = (kernelName) => {
        setSelectedKernelName(kernelName);
        setKernelFormData(data[kernelName]);
        setShowForm(true);
    };
    /**
     * Return Home on click.
     */
    const handleGoHome = () => {
        setShowForm(false);
        setAlertBox(false);
    };
    const refreshSchemas = () => {
        requestAPI("/schema", {
            method: "GET",
        }).then((s) => {
            setSchema(s);
        });
    };
    const refreshKernelspecs = () => {
        requestAPI("/", {
            method: "GET",
        }).then((res) => {
            setData(res);
            setCardData(createCardData(res));
        });
    };
    const handleCopyKernelspec = (kernel_name) => {
        requestAPI("/copy", {
            method: "POST",
            body: JSON.stringify({ name: kernel_name }),
        }).then((data) => {
            alert("A copy of " + kernel_name + " has been created.");
            refreshKernelspecs();
        });
    };
    const handleDeleteKernelspec = (kernel_name) => {
        requestAPI("/delete", {
            method: "POST",
            body: JSON.stringify({ name: kernel_name }),
        }).then((data) => {
            alert(kernel_name + " has been deleted.");
            refreshKernelspecs();
        });
    };
    /**
     * Handles a form submission when
     * kernels are modified in any form.
     *
     * Passed as a prop to Form.
     */
    const handleSubmitKernelspec = (e) => {
        requestAPI("/", {
            method: "POST",
            body: JSON.stringify({
                editedKernelPayload: JSON.stringify(e.formData),
                originalKernelName: selectedKernelName,
            }),
        }).then((data) => {
            if (data.success) {
                setAlertBox(true);
                refreshKernelspecs();
                setKernelFormData(e.formData);
            }
        });
    };
    /**
     * Generate the package of data needed
     * to display at the card selection screen.
     *
     * This method is called on when then data is generated to
     * send into the method generating the card data.
     */
    const createCardData = (ks) => {
        var arr = new Array();
        for (const property in ks) {
            arr.push({
                kernel_name: `${property}`,
                jupyter_name: `${ks[property].display_name}`,
            });
        }
        return arr;
    };
    useEffect(() => {
        refreshSchemas();
        refreshKernelspecs();
    }, []);
    return (React.createElement(Container, { fluid: true },
        React.createElement("div", null, "Kernelspec Manager"),
        !showForm &&
            React.createElement("div", { style: {
                    display: "flex",
                    flexWrap: "wrap"
                } }, cardData.map((cardPayload, idx) => (React.createElement(CardGrid, { handleSelectKernelspec: handleSelectKernelspec.bind(this), handleCopyKernelspec: handleCopyKernelspec.bind(this), handleDeleteKernelspec: handleDeleteKernelspec.bind(this), cardPayload: cardPayload, key: idx })))),
        alertBox &&
            React.createElement(SuccessAlertBox, { handleClose: handleGoHome }),
        showForm && (React.createElement(KsForm, { schema: schema, formData: kernelFormData, onSubmit: handleSubmitKernelspec.bind(this), onCancel: handleGoHome.bind(this) }))));
};
/**
 * KernelspecManagerWidget main class.
 */
export class KernelspecManagerWidget extends ReactWidget {
    constructor() {
        super();
        this.addClass("jp-Ksmm");
    }
    render() {
        return React.createElement(KernelManagerComponent, null);
    }
}
